/* 🌅 Fondo con degradado */
.consulta-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #f8e9e9, #ffffff);
}

/* 🎀 Título */
.consulta-title {
  font-size: 32px;
  font-weight: bold;
  color: #ff2a2a;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

/* 📦 Contenedor con efecto Glassmorphism */
.consulta-box {
  padding: 40px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
  width: 90%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

/* 📌 Grid */
.consulta-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 📌 Elementos de la Grid */
.consulta-grid-item {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* 🔎 Input */
.consulta-input {
  width: 100%;
  max-width: 350px;
}

/* 📌 Contenedor de botones en la misma fila */
.button-group {
  display: flex;
  justify-content: center;
  gap: 15px;
  width: 100%;
}

/* 🎯 Botón de consulta y regreso */
.consulta-button, .back-button {
  width: 200px;
  height: 50px;
  background: #ff2a2a !important;
  color: white !important;
  font-size: 16px !important;
  font-weight: bold !important;
  text-transform: uppercase !important;
  border-radius: 12px !important;
  transition: all 0.3s ease-in-out !important;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2) !important;
}

.consulta-button:hover, .back-button:hover {
  transform: scale(1.05);
  background: #ff5050 !important;
}

/* 📋 Resultados */
.consulta-result-box {
  padding: 20px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.3);
  text-align: left;
  width: 100%;
  max-width: 400px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

/* 📌 Colores de los textos en los resultados */
.consulta-result-box strong {
  color: #ff2a2a;
}

/* ❌ Mensaje de error */
.consulta-error {
  color: #d32f2f;
  font-weight: bold;
  background: #ffebee;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}

/* 📱 Responsividad */
@media (max-width: 768px) {
  .button-group {
      flex-direction: column;
      gap: 10px;
  }

  .consulta-button,
  .back-button {
      width: 100%;
  }
}
